.App {
  display: flex; 
  flex-direction: column; 
  align-items: center;
  height: 100vh; 
  padding: 20px;
}

  .header-row {
    display: grid;
    grid-template-columns: 158px 1fr 1fr 1fr 1fr 1fr; 
    margin-bottom: 10px;
    gap: 34px; 
    font-weight: bold;
  }
  
  .header-item {
    text-align: center;
    padding: 5px;
  }
  
  .game-board {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .guess-row {
    display: flex;
    align-items: center;
    margin-bottom: 0px;
  }
  
  .guess-name {
    width: 120px;
    color: #3498db;
    text-decoration: underline;
    margin-left: 0;
  }
  
  .target-loomian {
    margin-top: 20px;
    text-align: center;
  }
  
  .loomian-details {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .input-container {
    display: flex;
    margin-top: 20px;
  }
  
  .hint-icon {
    width: 100px;
    height: 100px;
    margin-left: 0px;
  }

  .guess-icon {
    width: 70px;
    height: 50px;
  }
  
  .loomian-dropdown {
    position: relative;
    display: flex;
    width: 100%;
  }
  
  .loomian-dropdown input {
    width: 300px;
    padding: 10px;
    font-size: 1em;
    border: 1px solid #ccc;
    border-right: none;
    border-radius: 4px 0 0 4px;
  }
  
  .loomian-dropdown button {
    padding: 10px 20px;
    font-size: 1em;
    border: 1px solid #ccc;
    border-left: none;
    border-radius: 0 4px 4px 0;
    background-color: #f0f0f0;
    cursor: pointer;
  }
  
  .loomian-dropdown button:hover {
    background-color: #e0e0e0;
  }
  
  .dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    border: 1px solid #ccc;
    background-color: #fff;
    max-height: 200px;
    overflow-y: auto;
    z-index: 1000;
    padding: 0;
    margin: 0;
    list-style: none;
  }
  
  .dropdown-item {
    display: flex;
    flex-direction: column;
    padding: 5px 10px;
    cursor: pointer;
  }
  
  .dropdown-item:hover {
    background-color: #f0f0f0;
  }
  
  .dropdown-name {
    font-size: 1em;
    font-weight: bold;
    text-align: center;
  }
  
  .dropdown-subdetails {
    font-size: 0.8em;
    color: #666;
    text-align: center;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
  }
  
  .dropdown-subdetails span {
    margin-right: 10px;
  }
  
  .guess-button {
    padding: 10px 20px;
    font-size: 1em;
    border: 1px solid #ccc;
    border-left: none;
    border-radius: 0 4px 4px 0;
    background-color: #f0f0f0;
    cursor: pointer;
  }
  
  .guess-button:hover {
    background-color: #e0e0e0;
  }

  .tooltip {
    position: absolute;
    background-color: #333;
    color: #fff;
    padding: 5px;
    border-radius: 4px;
    font-size: 0.9em;
    display: none;
    z-index: 1000;
    white-space: nowrap;
  }
  
  .tooltip-show {
    display: block;
  }

  .guess-name:hover .tooltip {
    display: block;
  }

  .hint-container {
    display: flex;
    align-items: center;
    gap: 0px;
  }
  
  .hint-value {
    color: #555;
  }

  @keyframes fadeDown {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  @keyframes fadeUp {
    from {
      opacity: 1;
      transform: translateY(0);
    }
    to {
      opacity: 0;
      transform: translateY(-50px);
    }
  }
  
  .guess-row.fade-down, .header-row.fade-down, .overlay.fade-down {
    animation: fadeDown 0.5s ease-in-out;
  }

  .overlay.fade-up {
    animation: fadeUp 0.5s ease-in forwards;
  }
  
  /** Game Over **/

  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
  .overlay-content {
    background: white;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  }
  
  .loomian-details {
    margin-top: 10px;
  }
  
  .loomian-details img {
    width: 140px;
    height: 100px;
  }
  
  .new-game-button, .share-button, .results-button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 16px;
    color: white;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }

  .results-button {
    background-color: #ffc107; 
  }

  .results-button:hover {
    background-color: #e0a800;
  }

  .new-game-button:hover, .share-button:hover, .results-button:hover {
    background-color: #0056b3;
  }

  .close-overlay-button {
    background-color: #dc3545; 
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 4px;
    cursor: pointer;
    position: absolute;
    top: 10px;
    right: 10px;
  }

  .close-overlay-button:hover {
    background-color: #c82333;
  }

  
  .new-game-button:hover, .share-button:hover {
    background-color: #0056b3;
  }
  
  .share-button {
    margin-left: 10px;
    background-color: #28a745;
  }
  
  .share-button:hover {
    background-color: #218838;
  }
  
  .share-result {
    margin-top: 20px;
    text-align: left;
    font-family: monospace;
  }
  
  .share-result p {
    color: darkgreen;
    font-weight: bold;
    text-align: center;
  }
  
  .type-badge {
    display: inline-block;
    padding: 5px;
    margin: 5px;
    border-radius: 5px;
    background-color: #f0f0f0; 
    text-align: center;
    transition: filter 0.3s ease;
  }
  
  .type-badge img {
    width: 24px;
    height: 24px;
  }
  
  .type-badge-container {
    margin-top: 20px;
    width: 800px;
  }

  .header-container {
    display: flex; 
    align-items: center; 
  }
  
  .header-container img {
    width: 80px; 
    height: auto; 
  }

  .App h1 {
    font-family: 'Arial', sans-serif; 
    font-size: 3rem; 
    font-weight: bold; 
    color: #2c3e50;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2); 
    margin: 0; 
    padding: 20px; 
    background: linear-gradient(135deg, #A4D9BC, #DE8998);
    -webkit-background-clip: text; 
    -webkit-text-fill-color: transparent; 
    border-radius: 8px;
    display: inline-block;
  }
  
  .App h1::before {
    content: ''; 
    position: absolute; 
    top: 50%; 
    left: 50%; 
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%; 
    background: rgba(255, 255, 255, 0.1); 
    border-radius: 8px; 
    z-index: -1; 
  }